#headerContainer {
    font-style: normal;
    font-weight: normal;
    line-height: normal;
    font-size: 4em;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -10%;
    transform: translate(-50%, -50%);
    color: #FFFFFF;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    width: 75%;
}

.CallToAction {
    font-size: 2em;
    font-family: 'Montserrat', sans-serif;
}

.topPage {
}

.header1 {
    font-family: 'Poppins', sans-serif;
    font-style: normal;
    font-weight: 200;
    font-size: 1em;
    line-height: normal;
    text-align: center;
    position: relative;
    color: #FFFFFF;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    margin: 0;
}

.header2 {
    font-family: 'Poppins', sans-serif;
    font-style: normal;
    font-weight: 800;
    font-size: 1.3em;
    line-height: normal;
    text-align: center;
    position: relative;
    color: #FFFFFF;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    margin: 0;
}

.Overlay {

    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
}

.Intro {
    background-color: #fdf9ee ;
    width: 100%;
}

.Intro img {
    background-color: #fdf9ee ;
    width: 100%;
}

.Intro h2 {
    position: static;
    text-align: center;
    font-family: 'Poppins', sans-serif;
    font-weight: 200;
    font-size: 60px;
}

.Intro h3 {
    position: static;
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    font-weight: 200;
    font-size: 45px;
}

.Intro h5 {
    position: static;
    text-align: center;
    font-family: 'Poppins', sans-serif;
    font-weight: 200;
    font-size: 40px;
}

.Unemployment {
    background-color: #fbfdf4;
}
.unemployment {
    width: 100%;
}

.unemployment img {
    width: 41.5%;
}

.image {
    text-align: left;
    width: 50%;
    display: block;
}

.text {
    text-align: right;
    width: 50%;
    display: block;
    position: relative;
}

.text h5 {
    font-family: 'Montserrat', sans-serif;
    font-weight: 200;
    font-size: 2em;
    display: 50%;
}

.text h3 {
    font-family: 'Montserrat', sans-serif;
    font-weight: 300;
    font-size: 3em;
}

.text h1 {
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    font-size: 4em;
}

.AdultInsecurity {
    position: static;
    background-color: #fffef5;
}

.foodInsecurity h3 {
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    font-weight: 100;
    font-size: 2em;
}

.foodInsecurity h2 {
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    font-size: 4em;
}
.children {
    position: static;
    background-color: #FFFDFA;
}
.children h1 {
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    font-size: 4em;
}

.children h3 {
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    font-weight: 100;
    font-size: 3em;
}

.but {
    width: 100%;
    height: 100%;
}

.But {
    text-align: center;
    font-size: 10em;
}
.Relief {
    width: 100%;
    height: 100%;
    background-color: #fffdf9;
}

.You {
    font-size: 258px;
    font-family: 'Poppins', sans-serif;
}

.reliefText {
    font-family: 'Montserrat', sans-serif;
    font-weight: 100;
    font-size: 4em;
}

.coffee {
    font-family: 'Montserrat', sans-serif;
    font-weight: 100;
    font-size: 30px;

} 

.Fight {
    height: 100%;    
}

.JoinFight {
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    text-align: center;
    font-size: 5.5em;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

